.albumImage{
    width: 70%;
    align-items: center;
    justify-content: center;
    position: relative;
    z-index: 1;
}

.albumImage img{
    border-radius: 30px;
    width: 90%;
    aspect-ratio: 1;
}

.albumImage_shadow{
    -webkit-filter: blur(10px);
    filter: blur(10px);
    width: 90%;
    position: absolute;
    z-index: -1;
    top: 20px;
}